$(document).ready(function(){
    $('#order_submit').fancybox({
        afterClose : function(){
            $('.order_buttom-input').val('');
        }
    });
});
$(document).ready(function() {

	$('.maincontent').fullpage({
		//options here
		autoScrolling: true,
        scrollHorizontally: true,
        scrollingSpeed: 950,
        anchors: ['firstPage', 'secondPage', 'thirdPage', 'fourthPage', 'fifthPage', 'sixthPage', 'seventhPage', 'lastPage'],
        menu: '.fixed_menu',
        loopHorizontal: false,
        // navigation: true,
        // responsiveWidth: 940,
        normalScrollElements: '.team_bottom-right-text, .menu_list-text, .hidden_text, .ymaps-2-1-76-balloon__content'
        
    });
    
    $('.down_arrow').on('click', function(e){
        e.preventDefault();

        fullpage_api.moveSectionDown();
    });
    
    $('.hero_slide-order-button, .burgers_right-button').on('click', function(e){
        e.preventDefault();

        fullpage_api.moveTo(7);
    });
    
    // $('.burgers_right-button').on('click', function(e){
    //     e.preventDefault();

    //     fullpage_api.moveTo(7);
    // });

    $('.fixed_menu-item, .slide_items-slider-click').on('click', function(e){
        e.preventDefault();

        var $this = $(this),
            href = parseInt($this.attr('href')) + 1;
        
        $.fn.fullpage.moveTo(href);

        // $this.closest('.fixed_menu-item').addClass('active')
        //     .siblings()
        //     .removeClass('active');
        
        $this.closest('li').addClass('active')
            .siblings()
            .removeClass('active');
        
    });

    // if ($(e.target).is('.team_bottom-right-text, .menu_list-text, .reviews_text'))return;

	// //methods
	// $.fn.fullpage.setAllowScrolling(false);
});
$(function(){
    $('.phone-mask').inputmask('+7 (999) 999 99 99');
});
$(document).ready(function(){
    $('#order_form-id').submit(function(e){
        e.preventDefault();

        var form = $(this),
            formDatas = form.serialize();

        $.ajax({
            type: 'POST',
            url: 'mail.php',
            data: formDatas
        }).done(function(){
            form.trigger('reset');
        });
        return false;
    });
});

$(document).ready(function(){
    $(".order_bottom-right-submit").on('click', function() {

        var form = $(this);
  
        $.fancybox.open({
          src  : '#done',
          type : 'inline',
          opts : {
            afterClose: function (){
                form.trigger('reset');
                console.log('close!');
            }
          }
        });
    });
});
// acco_team

$(function (){
    $('.team_bottom-batton').on('click', function(e){
        e.preventDefault();

        var $this = $(this),
            container = $this.closest('.team_bottom'),
            item = $this.closest('.team_bottom-list'),
            items = $('.team_bottom-list', container),
            otherContent = $('.team_bottom-block', container),
            content = item.find('.team_bottom-block'),
            reqHeight = item.find('.team_bottom-block-wrapper').outerHeight();

        if  (!item.hasClass('team_active')){
            items.removeClass('team_active');
            item.addClass('team_active');

            otherContent.css({
                'height' : 0
            });

            content.css({
                'height' : reqHeight
            });
        } else {
            item.removeClass('team_active');
            content.css({
                'height' : 0
            });
        }

    });
});

// acco_menu

$(function (){
    $('.menu_list-title').on('click', function(e){
        e.preventDefault();

        var $this = $(this),
            container = $this.closest('.menu_list'),
            item = $this.closest('.menu_list-block'),
            items = $('.menu_list-block', container),
            otherContent = $('.menu_list-description', container),
            content = item.find('.menu_list-description'),
            reqWidth = item.find('.menu_list-text').outerWidth();

        if  (!item.hasClass('menu_active')){
            items.removeClass('menu_active');
            item.addClass('menu_active');

            otherContent.css({
                'width' : 0
            });

            content.css({
                'width' : reqWidth
            });
            
        } else {
            item.removeClass('menu_active');
            content.css({
                'width' : 0
            });
        }

    });
});
